<script>
import { mapState } from 'vuex'
import { Toast, MessageBox } from 'mint-ui'
import { Loading } from 'vant'
// import service from '@/api'

export default {
  data () {
    return {
      phoneState: '', // 手机检验的状态
      passwordState: '', // 检验密码格式
      nickNameState: '', // 检验昵称格式
      phone: '', // 手机号
      nickName: '', // 昵称
      username: '', // 用户名即手机号
      password: '', // 密码
      passwordAgain: '', // 第二次输入的密码
      captcha: '', // 图形验证码
      isLogin: true, // 是否登录
      isForget: false, // 是否忘记密码
      agreeValue: false, // 注册时是否同意协议
      popupVisible: false, // 弹出层
      phoneCode: '', // 手机验证码
      loading: false, // 加载动画
      base64Data: '', // base64验证码
      codeId: '', // 图形验证码 codeId
      time60s: 0, // 手机发送短信间隔
      timer: null
    }
  },
  computed: {
    ...mapState({
      english: states => states.english,
      userName: states => states.user.userName,
      agreement: states => states.agreement
    }),
    code: function () {
      let code = window.sessionStorage.getItem('code')
      if (code) {
        return code
      }
    }
  },
  mounted () {
    if (this.code) {
      this.isLogin = false
      this.isForget = false
    }
    if (this.userName) {
      this.phone = this.uesrName
    }
    // 获取注册协议
    this.getAgreement()
  },
  watch: {
    isLogin: function (newVal, oldVal) {
      // 跳转时先把倒计时给清除了
      this.time60s = 0
      clearInterval(this.timer)
      this.timer = null
      // 不是登录（注册时获取图形验证码，并把手机号、密码、验证码清空）
      if (!newVal) {
        this.getCodeSrc()
      }
      this.phone = ''
      this.password = ''
      this.phoneCode = ''
      this.captcha = ''
    },
    isForget: function (newVal, oldVal) {
      // 跳转时先把倒计时给清除了
      this.time60s = 0
      clearInterval(this.timer)
      this.timer = null
      // 获取图形验证码，并把手机号、密码、验证码清空
      this.getCodeSrc()
      this.phone = ''
      this.password = ''
      this.phoneCode = ''
      this.captcha = ''
    }
  },
  methods: {
    // 获取图片验证码
    getCodeSrc () {
      let _this = this
      this.loading = true
      this.$store.dispatch('getCodebase64', {
        cb: function (err, data) { // eslint-disable-line
          if (data) {
            _this.base64Data = data.pngBase64
            _this.codeId = data.codeId
            _this.loading = false
          }
        }
      })
    },
    // 登录时，检查手机号和密码，给出提示，之后登录
    loginFn () {
      if (!this.phone) {
        MessageBox('', '请输入账号手机')
      }
      else if (!this.password) {
        MessageBox('', '请输入账号密码')
      }
      else {
        this.setLoginFn()
      }
    },
    toRegist () {
      this.isLogin = false
      this.isForget = false
    },
    tips (password) {
      // 字母数字特殊字符
      // let regex = new RegExp('(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]).{6,20}')
      let reg = /^[A-Za-z0-9]{6,20}$/  // 6-20 字母数字组合
      if (!reg.test(password)) {
        MessageBox('提示', '密码需由6-20位数字和字母组合而成！')
        return
      }
    },
    toast (msg, time) {
      Toast({
        message: msg,
        position: 'middle',
        duration: time
      })
    },
    registFn () {
      let _this = this
      this.tips(this.password)
      // 注册时，先验证图形验证码是否正确，但这里 getValidateCode 接口已经删除了
      if (this.nickName === '') {
        this.toast('昵称不能为空', 2000)
        return
      }
      if (this.agreeValue) { // 同意协议的情况下
        this.loading = true
        // 调取登录接口，如果拿到数据，跳转到 home
        _this.$store.dispatch('setRegist', {
          params: {
            userName: _this.phone,
            passWord: _this.password,
            verifyCode: _this.phoneCode,
            nickName: _this.nickName,
            code: _this.code ? _this.code : ''
          },
          cb: function (err, da) { // eslint-disable-line
            _this.loading = false
            if (da) {
              // 注册成功后调用登录的接口，成功登录
              _this.setLoginFn()
              window.sessionStorage.setItem('code', '')
            }
            else if (err) { // 注册失败，提示用户失败信息
              _this.toast(err.message, 2000)
            }
          }
        })
      }
      else { // 不同意协议的情况下，提示用户点击同意协议
        this.toast('请先同意协议', 2000)
      }
    },
    forgetFn () {
      this.isForget = true
    },
    forgetBackLogin () {
      this.isForget = false
    },
    registBackLogin () {
      this.isLogin = true
      this.isForget = false
    },
    changeLanguage () {
      this.$store.commit('ENGLISH', !this.english)
    },
    // 用户忘记密码，修改密码，先检查
    forgetOKFn () {
      if (this.phoneCode === '') {
        this.toast('短信码不能为空', 2000)
        return
      }
      this.tips(this.password)
      // 如果两次密码不同，提示
      if (this.password !== this.passwordAgain) {
        this.toast('密码不一致', 2000)
        return
      }
      this.setForgetPassword()
    },
    // 忘记密码，调修改密码的接口
    setForgetPassword () {
      let _this = this
      // 调接口
      this.$store.dispatch('setForgetpassword', {
        params: {
          userName: _this.phone,
          newPassword: _this.password,
          verifyCode: _this.phoneCode
        },
        cb: function (err, data) {
          _this.loading = false
          if (data) {
            // 接下来直接用账号密码登录
            _this.setLoginFn()
          }
          else if (err) {
            this.toast(err.message, 2000)
          }
        }
      })
    },
    // 输入手机号、输入图片验证码后，点击发送，给后台传手机号，图片验证码id和验证码
    sendPhoneCode () {
      let _this = this
      // 如果手机号为空
      if (this.phone === '') {
        this.toast('请输入手机', 1000)
        return
      }
      if (!this.checkPhone(this.phone)) {
        return
      }
      // 如果图片验证码为空
      if (this.captcha === '') {
        this.toast('请输入图形验证码', 1000)
        return
      }
      // 有倒计时并且不为0时
      if (this.time60s > 0) {
        this.toast('请' + _this.time60s + '秒后再试！', 1000)
        return
      }
      // 倒计时为0时，运行，给倒计时计为60s
      else {
        this.time60s = 60
        this.timer = setInterval(function () {
          if (_this.time60s > 0) {
            _this.time60s = _this.time60s - 1
          }
          else {
            clearInterval(_this.timer)
            _this.timer = null
          }
        }, 1000)
      }
      // 如果手机号和图形验证码都不为空
      if (this.phone !== '' && this.captcha !== '') {
        this.loading = true
        this.$store.dispatch('getSendMsg', {
          params: {
            mobile: _this.phone,
            codeId: _this.codeId,
            verifyValue: _this.captcha
          },
          cb: function (err, data) {
            _this.loading = false
            if (data) {
              // 手机号和图形验证码都正确
              _this.toast(data.message, 2000)
            }
            else if (err) {
              // 手机号和图形验证码不正确，给出提示，倒计时重置
              _this.toast(err.message, 2000)
              _this.time60s = 0
              clearInterval(_this.timer)
              _this.tiemr = null
            }
          }
        })
      }
    },
    // 调登录接口
    setLoginFn () {
      let _this = this
      // 登录接口
      this.loading = true
      // 调登录接口
      this.$store.dispatch('setLogin', {
        userName: _this.phone,
        passWord: _this.password,
        cb: function (err, data) {
          _this.loading = false
          if (data) { // 登录成功
            if (data.memberIsExist) { // 登录成功且用户存在时，跳转到 home
              window.sessionStorage.setItem('token', data.info.token)
              _this.$router.push({meta: {auth: true}, path: '/home'})
            }
            else { // 登录成功但用户不存在，提示用户先注册
              console.log(data, 'data里没有success和message')
              _this.toast(data.message, 2000)
            }
          }
          else if (err) { // 登录失败, 提示失败信息
            _this.toast(err.message, 2000)
          }
        }
      })
    },
    checkPhone (phone_address) {
      // var regex = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      var regex = /^((1[3-8][0-9])+\d{8})$/
      if (regex.test(phone_address)) {
        return true
      }
      else {
        this.toast('您输入的手机格式不正确', 2000)
        return false
      }
    },
    // 获取注册协议
    getAgreement () {
      this.$store.dispatch('getAgreement', {
        params: {
          type: 'regist'
        }
      })
    }
  },
  components: {
    vanLoading: Loading
  }
}
</script>
<template>
  <div class="login page">
    <!-- 登录 -->
    <div class="login-con" v-if="isLogin">
      <div class="lang">
        <mt-button 
          class="iconfont login-mt-button" 
          :class="{'icon-yingwenyuyan': english, 'icon-zhongwenyuyan': !english}" 
          @click.native="changeLanguage">
        </mt-button>
      </div>
      <!-- 正常登录 -->
      <div v-if="!isForget"> 
        <div class="tac login-btn">
          <div class="login-logo tac">
          </div>
          <div class="login-con-header-title">
            <span v-if="!english">登录</span>
            <span v-else>Login</span>
            <br>
            <span v-if="!english">太上熊猫</span>
            <span v-else>tsxm</span>
          </div>
        </div>

        <div>
          <div v-if="!english">
            <mt-field 
              class="login-field" 
              label="手机" 
              placeholder="请输入手机" 
              :state="phoneState" 
              v-model="phone">
            </mt-field>
            <mt-field 
              class="login-field" 
              label="密码" 
              placeholder="请输入密码" 
              type="password" 
              v-model="password">
            </mt-field>
          </div>
          <div v-else>
            <mt-field 
              class="login-field" 
              label="Phone" 
              placeholder="Please enter the phone" 
              :state="phoneState" 
              v-model="phone">
            </mt-field>
            <mt-field 
              class="login-field" 
              label="Password" 
              placeholder="Please enter the password" 
              type="password" 
              v-model="password">
            </mt-field>          
          </div>

          <div class="tac login-btn">
            <mt-button 
              class="login-mt-button"
              type="default" 
              @click.native="loginFn">
              <span v-if="!english">登录</span>
              <span v-else>Login</span>
            </mt-button>
            <mt-button 
              class="login-mt-button"
              type="default" 
              @click.native="toRegist">
              <span v-if="!english">注册</span>
              <span v-else>Regist</span>
            </mt-button>
            <mt-button 
              class="login-mt-button"
              type="default" 
              @click.native="forgetFn">
              <span v-if="!english">忘记密码</span>
              <span v-else>Forget</span>
            </mt-button>
          </div>
        </div>
      </div>
      <!-- 忘记密码 -->
      <div v-else>
        <div>
          <div class="tac login-btn">
            <div class="login-logo">
            </div>
            <div class="login-con-header-title">
              <span v-if="!english">忘记密码</span>
              <span v-else>Forget</span>
              <br>
              <span v-if="!english">太上熊猫</span>
              <span v-else>tsxm</span>
            </div>
          </div>

          <div class="login-con-body">
            <div v-if="!english">
              <mt-field 
                class="login-field" 
                label="手机" 
                placeholder="请输入手机" 
                v-model="phone">
              </mt-field>
              <mt-field 
                class="login-field" 
                placeholder="验证码"
                label="验证码" v-model="captcha">
                <img :src="base64Data" height="45px" width="100px" @click="getCodeSrc()">
              </mt-field>
              <mt-field 
                class="login-field login-phoneCode" 
                label="手机验证码" 
                placeholder="验证码" 
                v-model="phoneCode">
              </mt-field>
              <mt-button 
                class="login-phoneBtn login-mt-button"
                type="default" 
                @click.native="sendPhoneCode">
                <span v-if="!english">发送</span>
                <span v-else>send</span>
              </mt-button>
              <mt-field 
                class="login-field" 
                label="密码" 
                placeholder="请输入新密码" 
                type="password" 
                v-model="password">
              </mt-field>
              <mt-field 
                class="login-field" 
                label="密码" 
                placeholder="请再次输入新密码" 
                type="password" 
                v-model="passwordAgain">
              </mt-field>
            </div>

            <div v-else>
              <mt-field 
                class="login-field" 
                label="phone" 
                placeholder="Please enter the phone" 
                v-model="phone">
              </mt-field>
              <mt-field 
                class="login-field" 
                placeholder="verification code"
                label="verification code" v-model="captcha">
                <img :src="base64Data" height="45px" width="100px" @click="getCodeSrc()">
              </mt-field>
              <mt-field 
                class="login-field login-phoneCode" 
                label="phone code" 
                placeholder="Please enter the phone code" 
                v-model="phoneCode">
              </mt-field>
              <mt-button 
                class="login-phoneBtn login-mt-button"
                type="default" 
                @click.native="sendPhoneCode">
                <span v-if="!english">发送</span>
                <span v-else>send</span>
              </mt-button>
              <mt-field 
                class="login-field" 
                label="password" 
                placeholder="Please enter a new password" 
                type="password" 
                v-model="password">
              </mt-field>
              <mt-field 
                class="login-field" 
                label="password" 
                placeholder="Please enter your new password again" 
                type="password" 
                v-model="passwordAgain">
              </mt-field>
            </div>
            <div class="tac login-btn">
              <mt-button 
                class="login-mt-button"
                type="default" 
                @click.native="forgetOKFn">
                <span v-if="!english">确定</span>
                <span v-else>OK</span>
              </mt-button>
              <mt-button 
                class="login-mt-button"
                type="default" 
                @click.native="forgetBackLogin">
                <span v-if="!english">返回登录</span>
                <span v-else>Back</span>
              </mt-button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 注册第一步 第二步 -->
    <div class="login-con" v-else>
      <div>
        <div class="tac login-btn">
          <div class="login-logo">
          </div>
          <div class="login-con-header-title">
            <span v-if="!english">注册</span>
            <span v-else>Regist</span>
            <br>
            <span v-if="!english">太上熊猫</span>
            <span v-else>tsxm</span>
          </div>
        </div>

        <div class="login-con-body">
          <div v-if="!english">
            <mt-field class="login-field" 
              label="手机" 
              placeholder="请输入手机" 
             v-model="phone">
            </mt-field>
            <mt-field class="login-field" 
              label="验证码" 
              placeholder="验证码"
              v-model="captcha">
              <img :src="base64Data" height="45px" width="100px" @click="getCodeSrc()">
            </mt-field>
            <mt-field class="login-field login-phoneCode" 
              label="手机验证码" 
              placeholder="验证码" 
              v-model="phoneCode">
            </mt-field>
            <mt-button 
              class="login-phoneBtn login-mt-button"
              type="default" 
              @click.native="sendPhoneCode">
              <span v-if="!english">发送</span>
              <span v-else>send</span>
            </mt-button>
            <mt-field class="login-field" 
              label="昵称" 
              placeholder="请输入昵称" 
              v-model="nickName" :attr="{ maxlength: 10 }">
            </mt-field>
            <mt-field class="login-field" 
              label="密码" 
              placeholder="请输入密码" 
              type="password" v-model="password">
            </mt-field>
          </div>

          <div v-else>
            <mt-field class="login-field" 
              label="phone" 
              placeholder="Please enter the phone" 
             v-model="phone">
            </mt-field>
            <mt-field class="login-field" 
              label="verification code" 
              placeholder="verification code"
              v-model="captcha">
              <img :src="base64Data" height="45px" width="100px" @click="getCodeSrc()">
            </mt-field>
            <mt-field class="login-field" 
              label="phone code" 
              placeholder="Please enter the phone code" 
              v-model="phoneCode">
            </mt-field>
            <mt-button 
              class="login-phoneBtn login-mt-button"
              type="default" 
              @click.native="sendPhoneCode">
              <span v-if="!english">发送</span>
              <span v-else>send</span>
            </mt-button>
            <mt-field class="login-field" 
              label="nickname" 
              placeholder="Please enter the nickname" 
              v-model="username" :attr="{ maxlength: 10 }">
            </mt-field>
            <mt-field class="login-field" 
              label="password" 
              placeholder="Please enter the password" 
              type="password" v-model="password">
            </mt-field>
          </div>

          <div class="login-agreement-con">
            <input type="checkbox" v-model="agreeValue">
            <span>同意协议</span>
            <span 
              class="login-field login-viewprotocol"
              @click="popupVisible = true">
              <span v-if="!english">查看协议内容</span>
              <span v-else>right popup</span>
            </span>
          </div>
          <div class="tac login-btn">
            <mt-button 
              class="login-mt-button"
              type="default" 
              @click.native="registFn">
              <span v-if="!english">注册</span>
              <span v-else>Regist</span>
            </mt-button>
            <mt-button 
              class="login-mt-button"
              type="default" 
              @click.native="registBackLogin">
              <span v-if="!english">返回登录</span>
              <span v-else>Return</span>
            </mt-button>
          </div>
        </div>
      </div>
    </div>
    <div class="loading" v-if="loading">
      <van-loading type="spinner" color="black"></van-loading>
    </div>
    <mt-popup v-model="popupVisible" position="right" class="agree-popup" :modal="false">
      <mt-button 
        class="login-mt-button"
        @click.native="popupVisible = false" 
        size="large" type="default">
        <span v-if="!english">关闭协议</span>
        <span v-else>Close Protocol</span>
      </mt-button>
      <div v-if="agreement" class="login-protocol-con">
        <span v-if="!english">
          <div v-html="agreement"></div>
        </span>
        <span v-else>Protocol content</span>
      </div>
    </mt-popup>
  </div>
</template>

<style lang="scss" scoped>
.login {
  background: #00182c url(../assets/img/bg2.png) no-repeat;
  background-size: cover;
  // background: rgba(#ccc, 0.5);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  padding-bottom: 0;

  .lang {
    position: absolute;
    right: 10px;
    top: 10px;
  }

  .login-con {
    width: 80%;
  }

  .login-con-header-title {
    color: #fff;
    font-size: 28px;
  }

  .login-logo {
    display: block;
    margin: 0 auto;
    width: 100px;
    height: 100px;
    line-height: 100px;
    border-radius: 50px;
    border: 1px solid #ccc;
    background: #fff url(../assets/img/avatar.png) center no-repeat;
    background-size: 100% 100%;
  }
  .agree-popup {
    width: 100%;
    height: 100%;
    background-color: #fff;
  }
  .login-agreement-ck {
    display: inline-block;
    vertical-align: middle;
  }
  .login-agreement-con {
    // color: rgba(pink, 0.8);
    margin: 20px auto;
    input {
      vertical-align: middle;
    }
    .login-viewprotocol {
      cursor: pointer;
    }
  }
  .login-field {
    margin: 10px 0;
    border-radius: 5px;
    opacity: 0.8;
  }
  .login-mt-button {
    opacity: 0.8;
  }
  .login-btn {
    margin: 20px auto;
  }

  .login-phoneCode,
  .login-phoneBtn {
    display: inline-block;
    vertical-align: middle;
  }

  .login-phoneCode {
    width: 68%;
  }
  .login-phoneBtn {
    width: 30%;
  }

  .login-protocol-con {
    padding: 20px;
    height: calc(100% - 41px);
    overflow: auto;
  }
}
</style>


